<template>
  <div class="animated fadeIn">
    <b-row>
      <b-col>
        <b-card>
          <div slot="header">
            <b-link @click="goToCustomers">
              <i class="fa fa-user-o"></i> Customers:
            </b-link>
            <strong>{{customer.name}}</strong>'s Orders
            <b-link class="pull-right" @click="recalculateOrders">Recalculate Orders</b-link>
          </div>
        </b-card>
      </b-col>
    </b-row>
    <router-view></router-view>
    <!--/.row-->
  </div>
</template>

<script>
export default {
  name: "Orders",
  components: {},
  data() {
    return {
      customer: {}
    };
  },
  mounted: function() {
    this.loadCustomer();
  },
  computed: {},
  methods: {
    loadCustomer: function() {
      var customerId = this.$route.params.id;
      this.$fb.customerCollection
        .doc(customerId)
        .get()
        .then(ss => (this.customer = ss.data()));
    },

    goToCustomers: function() {
      this.$router.push({ name: "Customers" });
    },
    recalculateOrders: async function() {
      var orders = await this.loadOrders();
      console.log(orders);
      
    },
    loadOrders: async function() {
      return this.$fb.orderCollection
        .orderBy("dateCreated")
        .get()
        .then(ss => {
          if (ss.empty) return [];
          else {
            return ss.docs.map(doc => doc.data());
          }
        });
    }
  }
};
</script>
